import React from "react"
import Img from "gatsby-image/withIEPolyfill"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { useStaticQuery, graphql } from "gatsby"

import styles from "./NotFound.module.scss"

export default () => {
  const { treeWithRain, arrowContact } = useStaticQuery(graphql`
    query {
      treeWithRain: file(relativePath: { eq: "treeWithRain.png" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      arrowContact: file(relativePath: { eq: "arrowContact.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.container}>
        <div className={styles.pageContent}>
          <div className={styles.heading}>404 NOT FOUND</div>
          <div className={styles.description}>
            <div>This page is being constructed.</div>
            <div>
              <div>
                Please contact the builder if you are interested in sharing the
                development progress.
              </div>
              <div className={styles.ctaButtonContainer}>
                <Img
                  fixed={arrowContact.childImageSharp.fixed}
                  objectFit="cover"
                  objectPosition="50% 50%"
                  alt=""
                />
                <AniLink
                  fade
                  className={styles.ctaButton}
                  activeClassName={styles.ctaButtonHighlight}
                  to="/contact"
                >
                  <span className={styles.ctaButtonText}>Contact</span>
                </AniLink>
              </div>
            </div>
            <div className={styles.imageContainer}>
              <Img
                fluid={treeWithRain.childImageSharp.fluid}
                objectFit="cover"
                objectPosition="50% 50%"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}
